.Characters{
  max-width: 1200px;
  margin-block: 1rem;
  gap: 1rem;
  margin-left: 4rem;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.cardon{
    display: flex;
    flex-direction: column;
    width: clamp(10rem, calc(10rem + 1vw), 10rem);
    height: clamp(23rem, calc(23rem + 1vw), 23rem);
    margin-left: 1rem;
    margin-top: 1rem;
    overflow: hidden;
    box-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.1);
    border-radius: 1em;
    background: #ECE9E6;
   background: linear-gradient(to right, #FFFFFF, #ECE9E6);
  
}
.cardon:hover{
  box-shadow:0 10px 20px rgba(72, 241, 80, 0.747),0 4px 8px rgba(72, 241, 80, 0.747);
  -webkit-transform:scale(1.05);
  transform:scale(1.05)
}
.cardof{
  display: flex;
  flex-direction: column;
  width: clamp(10rem, calc(10rem + 1vw), 10rem);
  height: clamp(23rem, calc(23rem + 1vw), 23rem);
  margin-left: 1rem;
  margin-top: 1rem;
  overflow: hidden;
  box-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 1em;
  background:black;
 background: linear-gradient(to right, black, black);
 color: #fff;

}
.cardof:hover{
box-shadow:0 10px 20px #fff,0 4px 8px #fff;
-webkit-transform:scale(1.05);
transform:scale(1.05)
}


img {
    max-width: 100%;
    display: block;
    object-fit: cover;
  }
h2{
  font-size: larger;
  margin-bottom: 10px;
}
p{
  margin-top: 10px;
}
.pagination{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: -2rem;
}