button{
    border: none;
    cursor: pointer;
    border-radius: 12px;
    background-color: #fff;
    width: 5rem;
    height: 3rem;
    font-family: 'Roboto Serif', serif;
}
.of{
    background: black;
    color: #fff;
}
.of:hover{
    
    box-shadow:0 10px 20px #fff,0 4px 8px #fff;
    -webkit-transform:scale(1.05);
    transform:scale(1.05)
    
}
.on:hover:hover{
    box-shadow:0 10px 20px rgba(72, 241, 80, 0.747),0 4px 8px  rgba(72, 241, 80, 0.747);
    -webkit-transform:scale(1.05);
    transform:scale(1.05)
}
